@import "../../assets/styles/reset.scss";
@import "../../assets/styles/com.scss";
.footer {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    height: rem(83);
    background-color: #000;
    .nav {
        display: flex;
        & li {
            padding-top: rem(7);
            text-align: center;
            width: rem(75);
            height: rem(49);
            & img {
                height: rem(24);
                width: rem(24);
            }
            & p {
                font-weight: 600;
                color: #8890A1;
                font-size: rem(10);
            }
        }
    }
}
