@import "../../assets/styles/reset.scss";
@import "../../assets/styles/com.scss";
.header {
    display: flex;
    align-items: center;
    width: rem(375);
    padding: 0 rem(15);
    justify-content: space-between;
    height: rem(44);
    background: #191919;
    .logo {
        width: rem(120);
        height: rem(23);
    }
    .setWrap {
        display: flex;
        justify-content: center;
        align-items: center;
        .set {
            margin-left: rem(10);
            display: flex;
            align-items: center;
            width: rem(28);
            height: rem(28) ;
            background: #FFFFFF;
            border: rem(4) solid #fff;
            border-radius: 50%;
            & img {
                width: rem(20);
                height: rem(20);
            }
        }
        .wallet {
            margin-left: rem(10);
            & img {
                width: rem(28);
                height: rem(28);
            }
        }
    }
    :global {
        .ant-select {
            width: rem(75);
            height: rem(28);
            border-radius: rem(14);
            overflow: hidden;
        }
        .ant-select-selector {
            border: 0 !important;
            padding: 0 rem(3) 0 rem(8) !important;
        }
    }
}

